<template>
  <input 
  type="text"
  :value='modelValue'
  :disabled='isCorrect == undefined ? 0 : 1'
  @change="$emit('update:modelValue', $event.target.value)"
  :class='isCorrect == undefined ? "undefined" : isCorrect == 1 ? "correct" : "incorrect"'>
</template>

<script>
export default {
  props: ['isCorrect', 'modelValue']
}
</script>

<style lang="scss" scoped>
  input {
    margin: 15px 0;
    height: 1.8em;
    appearance: none;
    border: 2px solid;
    border-radius: 2px;
    font-family: 'Montserrat';
    text-indent: 10px;
  }
.undefined { border-color: lightgray }
.correct { border-color: green }
.incorrect { border-color: red }
</style>