import {createStore} from 'vuex'
import axios from 'axios'

export default createStore({
  state: () => ({ 
    levels: [],
    answers: [],
    levelIndex: undefined,
  }),
  mutations: {
    updateLevelIndex(state, data) { state.levelIndex = data },
    updateLevels(state, data) { state.levels.push(data) },
    updateAnswers(state, data) { state.levels[data.index].answers = data.body },
  },
  getters: {
    getLevels(state) { return state.levels },
    getAnswers(state) { return state.answers.length },
  },
  actions: {
    /*async fetchLevels(context) {
      try {
        axios.get('https://').then(res => context.commit('setLevels', res.data))
      }
      catch (err) { alert(err) }
    }*/
  },
})