<template>
  <div class="wrapper">
    <input 
      ref='input'
      type='radio' 
      name='pagination-panel'
      @click="$emit('taskGoto', this.task)"
      :class='ratio == undefined ? "undefined" : ratio == 1 ? "correct" : "incorrect"'
    />
    <span class="input-counter"><slot></slot></span>
  </div>
</template>

<script>
export default {
  props: ['ratio', 'task', 'taskIndex'],
  watch: {
    taskIndex(val) { if (this.task == val) this.$refs.input.click() }
  },
};
</script>

<style lang="scss" scoped>
@import "@/assets/main.scss";
.wrapper {
  position: relative;
  display: inline-block;
}
.input-counter {
  position: absolute;
  top: 16px;
  left: 16px;
  transform: translate(-50%, -50%);
  pointer-events: none;
  color: #323232;
  font-weight: bold;
  font-size: 1.2em;
}
input {
  appearance: none;
  position: relative;
  height: 32px;
  width: 32px;
  text-align: center;
  margin: 0 10px 0 0;
  border-radius: 2px;
  //background: rgba(black,.1);
  &:checked {
    background: #323232;
  }
  &:checked + span {
    color: white;
  }
}
</style>