<template>
  <router-view></router-view>
</template>

<script>
export default {
  created() {
    let script = document.createElement('script')
    script.setAttribute('type', "text/x-mathjax-config")
    script.text = "MathJax.Hub.Config( {tex2jax: { inlineMath: [['$', '$']] }} )"
    document.head.appendChild(script)
    script = document.createElement('script')
    script.setAttribute('src', 'http://cdn.mathjax.org/mathjax/latest/MathJax.js?config=default')
    document.head.appendChild(script)

    class Level {
      constructor(obj) {
        this.name = obj.name
        this.tasks = obj.tasks
      }
      get taskBools() { return this.answers.map((a,i) => a.map((e,j) => e==this.tasks[i].key[j])) }
      get taskRatios() { return this.taskBools.map(b => b.reduce((b1,b2) => b1+b2) / b.length) }
      get taskScores() { return this.taskRatios.map((a,i) => Math.round(a*this.tasks[i].weight)) }
      get levelScore() { return !this.answers ? 0 : this.taskScores.reduce((a,b) => a+b) }
      get levelScoreMax() { return this.tasks.map(a => a.weight).reduce((a,b) => a+b) }
      get levelRatio() { return !this.answers ? 0 : this.levelScore / this.levelScoreMax }
    }

    this.$store.commit("updateLevels",
      new Level ({
        name: "Прямі і площини у просторі",
        tasks: [
        {text: "Похила до площин утворює з цією площиною кут $30^\\circ$. Знайдіть довжину проекції похилої на цю площину, якщо довжина похилої дорівнює $6$ см.", options: ['$3\\sqrt{2}$ см','$3\\sqrt{3}$ см','$3$ см','$2$ см','$6$ см'], key: ['А'], weight: 10},
        {text: 'На малюнку зображено куб $ABCDA_1B_1C_1D_1$. Установіть відповідність між кутом та його градусною мірою.', options: ['$0^\\circ$','$30^\\circ$','$45^\\circ$','$60^\\circ$','$90^\\circ$'], items: ['Кут між прямими $A_1B_1$ і $DD_1$',"Кут між прямими $A_1C_1$ $C_1D$","Кут між прямою $A_1D$ і площиною $(ABC)$"], key: ["А","Б","В"], weight: 20, image: 'test.png'},
        {text: "$ABCD$ - ромб, сторона якого дорівнює 20 см. До площини ромба проведено перпендикуляр $AN$ завдовжки 5 см. Довжина діагоналі ромба $BD$ рівна 32 см. Знайдіть відстань (у см) від точки $N$ до прямої $BD$.", key: ["1"], weight: 20}
        ]
      }))


    this.$store.commit("updateLevels",
      new Level ({
        name: "Тригонометричні вирази",
        tasks: [
        {text: "Похила до площин утворює з цією площиною кут $30^\\circ$. Знайдіть довжину проекції похилої на цю площину, якщо довжина похилої дорівнює $6$ см.", options: ['$3\\sqrt{2}$ см','$3\\sqrt{3}$ см','$3$ см','$2$ см','$6$ см'], key: ['А'], weight: 10},
        {text: "Похила до площин утворює з цією площиною кут $30^\\circ$. Знайдіть довжину проекції похилої на цю площину, якщо довжина похилої дорівнює $6$ см.", options: ['$3\\sqrt{2}$ см','$3\\sqrt{3}$ см','$3$ см','$2$ см','$6$ см'], key: ['А'], weight: 10},
        {text: "$ABCD$ - ромб, сторона якого дорівнює 20 см. До площини ромба проведено перпендикуляр $AN$ завдовжки 5 см. Довжина діагоналі ромба $BD$ рівна 32 см. Знайдіть відстань (у см) від точки $N$ до прямої $BD$.", key: ["1"], weight: 20}
        ]
      }),
    )

  },
}
</script>

