<template>
  <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 120 120">
    <circle class='circle1' :style='`stroke: ${col}`' r=50 cx=60 cy=60></circle>
    <circle class='circle2' r=50 cx=60 cy=60 
      :stroke-dasharray='100*Math.PI*ratio + " " + 100*Math.PI' ref='circ'
      :style ='`stroke: ${col}; <!--animation: dash 2s linear forwards ${-2*(1-ratio)}s-->`'></circle>
    <text x=60 y=60 ref='txt'>0</text>
  </svg>
</template>

<script>
export default {
  name: 'progress-bar',
  props: ['ratio', 'col'],
  methods: {
    increment(i) {
      this.$refs.txt.textContent = i+'%'
      this.$refs.circ.style.strokeDashoffset = 3.14*(100*this.ratio - i)
      if (i < 100*this.ratio) setTimeout(this.increment.bind(null,i+1), 25*this.ratio)
    }
  },
  mounted() {
    for(let i = 0; i <= 100*this.ratio; i++) {
      this.$refs.txt.textContent = i+'%'
      this.$refs.circ.style.strokeDashoffset = 3.14*(100*this.ratio - i)
    }
  }
}
</script>

<style lang="scss" scoped>
svg {
  width: 100%;
  height: 100%;
}
text {
  fill: white;
  font-size: 22px;
  font-family: 'Montserrat';
  font-weight: 500;
  text-anchor: middle;
  alignment-baseline: middle;
}
circle {
  fill: none; 
  transform: rotate(-90deg); 
  transform-origin: center;
}
.circle1 {
  stroke: white;
  stroke-width: 5px; 
  stroke-opacity: .24;
}
.circle2 {
  stroke: white;
  stroke-width: 7px; 
  stroke-linecap: round;
}
</style>